.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.navbar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 2px solid rgba(56, 56, 192, 0.767);
}

.BookScrollViewContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  outline: solid 2px green;
}

button {
  padding: 10px;
  border-radius: 3px;
  -webkit-box-shadow: 0 1px 1px gray;
          box-shadow: 0 1px 1px gray;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

button:hover {
  color: white;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  background-color: rgb(71, 101, 182);
  -webkit-box-shadow: rgba(71, 101, 182, 0.548);
          box-shadow: rgba(71, 101, 182, 0.548);
  border-color: rgba(71, 101, 182, 0.795);
}

input {
  height: 25px;
  font-size: 15px;
}

.hidden {
  display: none;
}

.delete-btn { 
  background-color: white;
  color: red;
  border: 1px solid black;
  -webkit-transition: .2s all ease;
  -o-transition: .2s all ease;
  transition: .2s all ease;
}

.delete-btn:hover {
  background-color: red;
  color: white;
  -webkit-transition: .2s all ease;
  -o-transition: .2s all ease;
  transition: .2s all ease;
}

.invalid {
  outline: red 1.5px solid;
}

.addToBookBtn:hover {
  -webkit-transition: .2s all ease;
  -o-transition: .2s all ease;
  transition: .2s all ease;
  background-color: rgb(78, 157, 226);
  color: aliceblue(235, 235, 235)
}


